.metismenu {
  padding: 0;
}

#sidebar-menu .menu-title {
  padding: 10px 20px;
  letter-spacing: 0.05em;
  pointer-events: none;
  cursor: default;
  font-size: 0.6875rem;
  text-transform: uppercase;
  color: #adb5bd;
  font-weight: 500;
}

.metismenu li {
  list-style: none;
}

#sidebar-menu > ul > li > a {
  color: #435966;
  display: block;
  padding: 12px 20px;
  position: relative;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  font-size: 0.925rem;
}

#sidebar-menu > ul > li > a i {
  display: inline-block;
  line-height: 1.0625rem;
  margin: 0 10px 0 3px;
  text-align: center;
  vertical-align: middle;
  width: 20px;
}

#sidebar-menu > ul > li > a > span {
  vertical-align: middle;
  margin-left: 8px;
}

#sidebar-menu > ul > li > a.active {
  color: #71b6f9;
}

#sidebar-menu > ul > li > ul {
  padding-left: 40px;
}

.metismenu .collapse.in {
  display: block;
}

.metismenu .collapse {
  display: none;
}

.collapse:not(.show) {
  display: none;
}

.metismenu ul li {
  width: 100%;
}

.nav-second-level li.active > a,
.nav-third-level li.active > a {
  color: #71b6f9;
}

.nav-second-level li a,
.nav-thrid-level li a {
  padding: 8px 20px;
